.ant-menu {
    &.header__menu {
        border: none;

        .ant-menu-item {
            margin-bottom: 0;
            letter-spacing: 0.08em;

            &-selected {
                background-color: transparent;

                a {
                    color: @black;
                }
            }

            &-content {
                overflow: none;
            }

            &:active {
                background-color: transparent;
            }

            a {
                position: relative;

                &:hover {
                    color: @black;
                }
            }
        }
    }
}

.ant-form-item {
    &.input-field {
        display: block;
    }
}

.ant-input {
    &-affix-wrapper-focused,
    &:focus {
        border-color: @black;
        box-shadow: none;
    }
}

a.ant-typography, 
.ant-typography a {
    color: @gray;

    &.text-underline {
        text-decoration: underline;

        &:hover {
            text-decoration: none;
        }
    }

    &:hover {
        color: inherit
    }
}

.ant-btn {
    &.auth-form__submit {
        width: 100%;
    }

    &-primary {
        &:hover {
            opacity: 0.9;
        }
    }
}